    lyric2: `Monet’s Garden
W:   Claude Monet was a great artist. He was the first of a group of artists called the
Impressionists. This group of artists thought about how things in the world made them feel. They tried to show this in their work. This way of painting shows how light can change shapes and colors.
Monet did a lot of his work outdoors. He wanted to show how daylight made things look. Many of his paintings are of gardens. He liked to paint flowers and trees. He loved to paint in his own garden. His garden was in a village. He lived there for many years.
Today, many visitors come each year to this garden. They come from all over the world.
Most of them are art lovers or artists. They come to see the place that Monet loved so much.`
